@font-face {
  font-family: "Ubuntu";
  src: url("../fonts/Ubuntu-Bold.woff2") format("woff2"), url("../fonts/Ubuntu-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Ubuntu";
  src: url("../fonts/Ubuntu-Regular.woff2") format("woff2"), url("../fonts/Ubuntu-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  height: 100%;
  box-sizing: border-box; }

body {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  font-family: "Ubuntu", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  color: #333333; }

main {
  flex-grow: 1; }

header {
  flex-grow: 0; }

footer {
  flex-grow: 1; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.hidden {
  display: none !important; }

.container {
  margin: 0 auto;
  width: 1680px;
  max-width: 100%;
  padding: 0 70px; }

.h2 {
  margin: 0;
  width: 520px;
  max-width: 100%;
  font-family: "Ubuntu", "Arial", sans-serif;
  font-weight: 700;
  font-size: 40px;
  line-height: 1.33;
  letter-spacing: 0.11rem; }

@media (max-width: 900px) {
  .h2 {
    font-size: 30px;
    letter-spacing: normal; } }

.hamburger {
  width: 30px;
  height: 30px;
  position: relative; }

.hamburger__bar {
  padding: 0;
  width: 30px;
  height: 4px;
  background-color: #393e6b;
  display: block;
  border-radius: 4px;
  transition: all 0.4s ease-in-out;
  position: absolute; }

.hamburger__bar--bar1 {
  top: 0;
  transform-origin: 5%; }

.hamburger__bar--bar2,
.hamburger__bar--bar3 {
  top: 13.5px; }

.hamburger__bar--bar3 {
  right: 0;
  display: none; }

.hamburger__bar--bar4 {
  bottom: 0;
  transform-origin: 5%; }

/* Стили для активной кнопки*/
.js-btn-active .hamburger__bar--bar1 {
  transform: rotate(45deg);
  height: 3px;
  width: 42px; }

.js-btn-active .hamburger__bar--bar3 {
  display: inline;
  transform: rotate(45deg);
  height: 3px;
  background-color: transparent; }

.js-btn-active .hamburger__bar--bar2 {
  transform: rotate(-45deg);
  height: 3px;
  background-color: transparent; }

.js-btn-active .hamburger__bar--bar4 {
  transform: rotate(-45deg);
  height: 3px;
  width: 42px; }

.header {
  position: relative; }

.header__row {
  padding-top: 30px;
  padding-bottom: 30px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  background-image: linear-gradient(155deg, #4c9db3 0%, rgba(45, 225, 207, 0.9) 23%, rgba(175, 212, 210, 0.7) 24%, #ffffff 27%, #ffffff 100%);
  box-shadow: 0 2px 10px 0px #bfc3c4; }

.header__column {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  flex-grow: 1; }

.header__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex; }

.header__item {
  margin-right: 35px; }

.header__nav-item {
  margin-right: 22px; }

.header__logo {
  flex-grow: 0; }
  .header__logo:hover, .header__logo:focus, .header__logo:active {
    cursor: pointer;
    text-decoration: underline;
    opacity: 0.6; }

.header__link {
  font-family: "Ubuntu", "Arial", sans-serif;
  font-weight: 400;
  color: #393e6b;
  text-transform: uppercase; }
  .header__link:hover, .header__link:focus, .header__link:active {
    cursor: pointer;
    text-decoration: underline;
    opacity: 0.6; }

.header__hamburger {
  display: none; }

@media (max-width: 1250px) {
  .header__item {
    margin-right: 15px; }
  .header__nav-item {
    margin-right: 15px; } }
